@import '../../../global.css';
.parallax {
  overflow: hidden;
  letter-spacing: -2px;
  line-height: 0.8;
  margin: 0;
  white-space: nowrap;
  display: flex;
  flex-wrap: nowrap;
}

.parallax .scroller {
  font-weight: 600;
  text-transform: uppercase;
  font-size: 64px;
  display: flex;
  white-space: nowrap;
  display: flex;
  flex-wrap: nowrap;
  background: var(--light);
  color: var(--dark);
}
@media (max-width: 768px) {
  .parallax .scroller {
    font-size: 35px;
  }
}

.item {
  padding: 20px;
}/*# sourceMappingURL=style.module.css.map */